.app {
  background-image: radial-gradient(silver,lightblue,blue);
  text-align: center;
  width: 100%;
  margin: 0;
  outline: 0;
  position: relative;
}
body{
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 468px) {
    .app{
      width: 100%;
      display: flex;
      flex-direction: column;
      
    }
}